import React, { useState } from "react";
import { Header } from "../../components/Header";
import FilterItem from "../../components/FilterItem";
import CollapsiblePanel from "../../components/CollapsiblePanel";
import Footer from "../../components/Footer";
import LinkButton from "../../components/LinkButton";
import BreadCrumbs from "../../components/BreadCrumbs";

import css from "./style.module.scss";

const filters = [
  {
    name: "",
  },
  {
    name: "  ",
  },
  {
    name: "  ",
  },
];

const FAQ = () => {
  const [activeFilters, setActiveFilters] = useState<number[]>([]);

  const onClickFilter = (index: number, toggleAll: boolean) => {
    setActiveFilters((prevFilters) => {
      if (toggleAll) {
        return prevFilters.length === filters.length
          ? []
          : filters.map((filter, index) => index);
      }
      return [index];
    });
  };

  return (
    <>
      <Header />
      <div className={css.layout}>
        <BreadCrumbs
          items={[
            { path: "/", breadcrumb: "" },
            { path: "/questions", breadcrumb: "FAQ" },
          ]}
        />
        <h2 className={css.title}>FAQ</h2>
        <p className={css.subtitle}>  </p>
        <div className={css.filters}>
          {filters.map((item, index) => (
            <FilterItem
              key={index}
              filter={item}
              active={activeFilters.includes(index)}
              onClick={onClickFilter}
              index={index}
            />
          ))}
        </div>
        <div className={css.questions}>
          <CollapsiblePanel
            title="    -?"
            description="    -    .   ,            ,        .     -          ."
          />
          <CollapsiblePanel
            title="     -,   ?"
            description="    -    .   ,            ,        .     -          ."
          />
          <CollapsiblePanel
            title="    ?"
            description="    -    .   ,            ,        .     -          ."
          />
          <CollapsiblePanel
            title="    ?"
            description="    -    .   ,            ,        .     -          ."
          />
          <CollapsiblePanel
            title="     ?"
            description="    -    .   ,            ,        .     -          ."
          />
        </div>
        <div className={css.connection}>
          <div className={css.container}>
            <h2 className={css.title}>   !</h2>
            <p className={css.text}>
                   <br />   
              .
            </p>
            <div className={css.button}>
              <LinkButton value=" " style="primary" />
            </div>
          </div>
        </div>
      </div>
      <Footer />
    </>
  );
};

export default FAQ;
