interface CoursePageProps {
  id: string;
}

const CoursePage = ({ id }: CoursePageProps) => {
  const size = useWindowSize();

  const { data: feedbacks = [] } = useQuery({
    queryKey: ["feedbacks"],
    queryFn: getFeedbacks,
  });

  const { data: course, isLoading } = useQuery({
    queryKey: ["course", id],
    queryFn: getCourse,
  });

  const { data: similarCourses = [] } = useQuery({
    queryKey: ["similarCourses"],
    queryFn: getSimilarCourses,
  });

  if (isLoading) {
    return null;
  }

  if (!course) {
    return <div>  </div>;
  }

  const {
    language,
    level,
    img,
    price,
    lessons,
    tasks,
    tests,
    expertHours,
    color,
  } = course;

  return (
    <>
      <Header />
      <div className={css.layout}>
        <BreadCrumbs
          items={[
            { path: "/", breadcrumb: "" },
            { path: "/courses", breadcrumb: "" },
            { path: "/courses/course-page", breadcrumb: " " }, // id
          ]}
        />
        <div className={css.coursItem}>
          <div className={css.infoBlock}>
            <div className={css.wrapper}>
              <h2 className={css.name}>
                {language} <br />  {level} 
              </h2>
              <h3 className={css.title}> </h3>
              <div className={css.list}>
                <span className={css.listTitle}>  :</span>
                <li className={css.item}>
                  -    ,   
                </li>
                <li className={css.item}>
                  -        
                   ,
                </li>
                <li className={css.item}>
                  -      .
                </li>
                <li className={css.item}>
                  -       !
                </li>
              </div>
              <div className={css.subscription}>
                <div className={css.level}> </div>
                <div className={css.socials}>
                  <p className={css.text}>:</p>
                  <div className={css.socialsBlock}>
                    <FacebookIcon fill="#78258D" />
                    <YouTubeIcon fill="#78258D" />
                    <TwitIcon fill="#78258D" />
                    <VkIcon fill="#78258D" />
                  </div>
                </div>
              </div>
            </div>
            <div className={css.wrapper}>
              <h3 className={css.title}> </h3>
              <div className={css.modules}>
                {modules.map((module) => (
                  <ModuleItem
                    title={module.title}
                    topic={module.topic}
                    lessons={module.lessons}
                    description={module.description}
                  />
                ))}
              </div>
            </div>
            <div className={css.wrapper}>
              <h3 className={css.title}>  </h3>
              <div className={css.formatBlock}>
                <div className={css.card}>
                  <img src="../img/format1.png" alt="" />
                  <span className={css.description}>
                    - <br />     
                  </span>
                </div>
                <div className={css.card}>
                  <img src="../img/format2.png" alt="" />
                  <span className={css.description}>
                     <br />  
                    <p>(3D-    )</p>
                  </span>
                </div>
                <div className={css.card}>
                  <img src="../img/format3.png" alt="" />
                  <span className={css.description}>
                        <br />  
                  </span>
                </div>
              </div>
            </div>
            <div className={css.wrapper}>
              <div className={css.container}>
                <h3 className={css.title}>  </h3>
                <LinkButton value=" " />
              </div>
              <div className={css.feedbacks}>
                {feedbacks.map((item) => (
                  <FeedbackCard
                    name={item.name}
                    feedback={item.description}
                    color={item.color}
                    stars={item.stars}
                  />
                ))}
              </div>
            </div>
            <div className={css.wrapper}>
              <h3 className={css.title}> </h3>
              <div className={css.similarCourses}>
                {similarCourses.map((card) => (
                  <Card item={card} width={514} link={`/courses/${card.id}`} />
                ))}
              </div>
            </div>
          </div>
          <div className={css.moreInformation}>
            <CardPrice
              img={img}
              price={price}
              lessons={lessons}
              tasks={tasks}
              tests={tests}
              expertHours={expertHours}
              color={color}
            />
            {/* {size.width > 1000 ? ( */}
            <div className={css.rating}>
              <TopStudents />
            </div>
            {/* ) : null} */}
          </div>
        </div>
        <div className={css.registration}>
          <RegistrationBlock />
        </div>
      </div>
      <Footer />
    </>
  );
};

const Wrapper = () => {
  const params = useParams();
  if (!params.id) {
    return null;
  }
  return <CoursePage id={params.id} />;
};

export default Wrapper;
